.greetingForm {

}

.inputAndButtonContainer {

    margin: 9px 0 0 70px;
    display: flex;
    width: 372px;
    height: 36px;
    /*justify-content: center;*/

    flex-shrink: 0;
}

.error {

    color: #C00;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.input {
    border: 1px solid #D1D1D1 ;
    border-radius: 4px;
    display: flex;
    width: 372px;
    height: 36px;
    justify-content: center;
    align-items: center;
}

.input:focus {

}

.errorInput {
    border: 1px solid red ;
    border-radius: 4px;
    display: flex;
    width: 372px;
    height: 36px;
    justify-content: center;
    align-items: center;
}

.button {
    /*border-radius: 3px;*/
    /*opacity: 0.5;*/
    border: none;
    background: #0066CC;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px; /* 142.857% */
    color: #fff;
    display: inline-flex;
    height: 36px;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
    margin-left: 12px;
}

.button:focus {

}

.button:active {
    color:#FFFFFF;
    display: inline-flex;
    height: 36px;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
    border-radius: 3px;
    background-color: #05468a;
    border: none;
}

.button:disabled {

    color:#FFFFFF;
    display: inline-flex;
    height: 36px;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
    border-radius: 3px;
    background-color: #0066CC;
    opacity: 0.5;
}

.text {

    color: rgba(0, 0, 0, 0.50);
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-left: 70px;
}

.greeting {

    margin: 20px 0 0 70px
}
