.burgerMenuIcon {
    margin: 20px 0 16px 70px;
    display: flex;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;

}

.header {
    justify-content: space-between;
    display: flex;
    background: #FFF;
    box-shadow: 0px 0px 40px 0px rgba(29, 33, 38, 0.13), 0px 0px 2px 0px rgba(29, 33, 38, 0.10);
    height: 60px;

}

.header h1 {
    color: #000;
    font-family: Montserrat;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    display: flex;
    justify-content: flex-end;
    margin: 17px 70px 14px 0;
}